















































	export default {
// 		props: {
// 			count: {
// 				type: Number,
// 				default: 2
// 			}
// 		},
		data() {
			return {
				count:2,
				imageList: [],
				picId:[],
				taskData:{
					taskTitle:"",  //标题
					copyWriting:"", //文案
					contributionNo:'', //发布任务贡献值
					releaseTaskNo:"",  //发布任务数量
					allMoney:"",  //总数
					picId:"",   //图片1
					picId2:"",  //图片2
				},
				taskIdentifier:"", //任务编号
				vipLevel:"",
				userInfo:{}
			};
		},
		onUnload() {
			this.imageList = [];
		},
		onLoad() {
			this.vipLevel = uni.getStorageSync('vipLevel') ?uni.getStorageSync('vipLevel') :"";
			this.userInfo = uni.getStorageSync('userInfo');
		},
		
		methods:{
			testNumber(){
				this.taskData.contributionNo = this.taskData.contributionNo.replace(/[^\.\d]/g,'');
				this.taskData.contributionNo = this.taskData.contributionNo.replace('.','');
			},
			testNumber2(){
				this.taskData.releaseTaskNo = this.taskData.releaseTaskNo.replace(/[^\.\d]/g,'');
				this.taskData.releaseTaskNo = this.taskData.releaseTaskNo.replace('.','');
			},
			publishTask(){
				var that = this;
				this.taskData.allMoney = Number(this.taskData.contributionNo)*Number(this.taskData.releaseTaskNo);
				this.taskData.picId = this.picId[0];
				this.taskData.picId2 = this.picId[1];
				// console.log(this.taskData,this.picId);
				this.taskData.account = this.userInfo.account;
				// conosole.log(this.taskData)
				if(this.taskData.contributionNo ==''||Number(this.taskData.contributionNo) < 1){
					uni.showToast({
						icon:'none',
						title:'单笔广告费用不能少于1'
					})
					return;
				}
				if(this.taskData.releaseTaskNo ==''||Number(this.taskData.releaseTaskNo) < 1 ){
					uni.showToast({
						icon:'none',
						title:'广告数量至少为1'
					})
					return;
				}
				const jsonString = {
					taskInfo:this.taskData,
					requestType:"creatTask",
				}
				const param ={
					controllerRequestType:"taskControllerService",
					jsonString:JSON.stringify(jsonString)
				}
				uni.request({
					url:that.websiteUrl,
					method:'POST',
					header: {
						"content-type":"application/x-www-form-urlencoded"
					},
					data:param,
					success: (res) => {
						if(res.data.errorCode == '0000'){
							if(res.data.taskInfo.statusTask == '04'){
								console.log(res)
								console.log("间隔符")
								console.log(res.data)
								that.taskIdentifier = res.data.taskInfo.taskIdentifier;
								
								uni.showModal({
									title:'微品提示',
									content:'确定支付'+res.data.taskInfo.allMoney + '发布自由任务',
									success:function(ress){
										 // '+res.data.taskInfo.allMoney +'
										if (ress.confirm) {
											uni.navigateTo({
												url:'../MoneyWay/request-payment?money=0.01&payTradeType=' + res.data.taskInfo.taskIdentifier,
											})
										} else if (ress.cancel) {
											uni.reLaunch({
													url:'../main/main',
											});
										}
										
									}
								})
							
							}else{
								uni.showModal({
								content: '勇士任务发布成功',
								success: function (res) {
								uni.reLaunch({
										url:'../main/main',
								});
								}
							});
							}
						}else{
							uni.showToast({
								icon:'none',
								title:res.data.errorMessage
							})
						}
					},
					fail:(res) =>{
						uni.showToast({
							icon:'none',
							title:'网络异常，请重试'
						})
						console.log(JSON.stringify(res));
					}
				})
			},
			totalPrice(){
				var  that = this;
				this.taskData.allMoney = Number(this.taskData.contributionNo)*Number(this.taskData.releaseTaskNo);
			},
			urlTobase64(url){
				var that = this;
				uni.request({
					url:url,
					method:'GET',
					responseType: 'arraybuffer',
					success: ress => {
						that.base64 = wx.arrayBufferToBase64(ress.data); //把arraybuffer转成base64 
						that.base64 = 'data:image/jpeg;base64,' + that.base64 //不加上这串字符，在页面无法显示的哦
					}
				})
			},
			chooseImg() {
				var that = this;
				//选择图片
				uni.chooseImage({
					sourceType: ['camera', 'album'],
					sizeType: 'compressed',
					count: that.count - that.imageList.length,
					success: function(res) { 
						console.log(JSON.stringify(res.tempFilePaths))
						uni.request({
							url:res.tempFilePaths[0],
							method:'GET',
							responseType: 'arraybuffer',
							success: ress => {
								var base64 = wx.arrayBufferToBase64(ress.data); //把arraybuffer转成base64 
								// base64 = 'data:image/jpeg;base64,' + base64 //不加上这串字符，在页面无法显示的哦
								// that.base64 = base64;
								const jsonString = {
									taskInfo:{
										picBast64:base64,
										upType:'pic',
									},
									requestType:"upFile",
								}
								const param ={
									controllerRequestType:"taskControllerService",
									jsonString:JSON.stringify(jsonString)
								}
								uni.request({
									url:that.websiteUrl,
									method:'POST',
									header: {
										"content-type":"application/x-www-form-urlencoded"
									},
									data:param,
									success: (res) => {
										if(res.data.errorCode == '0000'){
											console.log('chenggong')
											that.picId.push(res.data.taskInfo.picId);
										}
									},
									fail:(res) =>{
										console.log(JSON.stringify(res));
									}
								})
								
							}
						})
						
						// that.base64 = that.urlTobase64(res.tempFilePaths[0]);
					
						// self.imageList = self.imageList.concat(res.tempFilePaths);
						//只添加不重复的文件
						if (that.imageList.length > 0) {
							for (let img of res.tempFilePaths) {
								let index = that.imageList.findIndex(item => item === img);
								if (index === -1) {
									that.imageList.push(img);
								}
							}
						} else {
							that.imageList = res.tempFilePaths;
						}
						
						// console.log(JSON.stringify(self.imageList))
						//触发所选择文件更改事件，参数为文件列表
						that.$emit('change', that.imageList);
					}
				});
			},
			removeImage(img) {
				var that = this;
				uni.showModal({
					content: '确认删除选中的图片？',
					success: function() {
						const index = that.imageList.findIndex(item => item === img);
						that.imageList.splice(index, 1);
					}
				});
			},
			previewImage() {
				//预览图片
				uni.previewImage({
					urls: this.imageList
				});
			}
		}
	}



